import { Button } from '@material-ui/core';
import { useState } from 'react';


function RandomMeme({memes}) {
    const [RandomMeme, setRandomMeme] = useState(memes[Math.floor(Math.random() * memes.length)]);

  return (
    <>
    <p>Here you just get a random meme out of the list, not sure how useful this is but well you can use it if you want, just click the button to get a new random one.
    P.S. I hope that you are having a nice day.
    </p>
    <Button variant="outlined" onClick={()=>{
        setRandomMeme(current => {
            let meme = memes[Math.floor(Math.random() * memes.length)]
            while(meme == current){
                let meme = memes[Math.floor(Math.random() * memes.length)]
            }

            return meme
        })
    }}>
        Random meme
    </Button>
    <img style={{ maxWidth: "85%", maxHeight: "650px", margin: "auto", display: "block", marginTop: "25px" }} src={RandomMeme?.image} />
    </>
  );
}

export default RandomMeme;
