import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import ListIcon from '@material-ui/icons/List';import SlideshowIcon from '@material-ui/icons/Slideshow';
import ImageIcon from '@material-ui/icons/Image';
import MemePage from './MemePage';


import meme1 from './images/8HEUJsFEwRjUoAAAAASUVORK5CYII.png';
import meme2 from './images/a1rdGzY_700bwp.png';
import meme3 from './images/Aikf9VBeWk8kAAAAAElFTkSuQmCC.png';
import meme4 from './images/doggs-banner.png';
import meme5 from './images/enjoy-her-talking-about-day.png';
import meme6 from './images/GN5PnG6oSHcAAAAASUVORK5CYII.png';
import meme7 from './images/H0r8H8Ad7QlwVjSA8AAAAASUVORK5CYII.png';
import meme8 from './images/P96mWEOkersSAAAAAElFTkSuQmCC.png';
import meme9 from './images/unknown.png';
import meme10 from './images/w8kSB8Wbl6GDAAAAABJRU5ErkJggg.png';
import meme11 from './images/z20cNZoMJsAAAAASUVORK5CYII.png';
import meme12 from './images/zAAAAAElFTkSuQmCC.png';
import meme13 from './images/zcute.png';
import meme14 from './images/miia_best_quiz_question.png';
import meme15 from './images/art_jealous_gf.png';
import meme16 from './images/status_meme.png';
import meme17 from './images/friends_miia_best.png';
import meme18 from './images/depression_love.png';
import meme19 from './images/happiness_stonks.png';
import Slideshow from './SlideShow';
import RandomMeme from './RandonMeme';

const useStyles = makeStyles({
  root: {
    width: '100%',
    position: 'fixed',
    bottom: 0,
  },
});

export default function MemeMenu() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);


  let memes = [
    {
      image: meme1
    },
    {
      image: meme2
    },
    {
      image: meme3
    },
    {
      image: meme4
    },
    {
      image: meme5
    },
    {
      image: meme6
    },
    {
      image: meme7
    },
    {
      image: meme8
    },
    {
      image: meme9
    },
    {
      image: meme10
    },
    {
      image: meme11
    },
    {
      image: meme12
    },
    {
      image: meme13
    },
    {
      image: meme14
    },
    {
      image: meme15
    },
    {
      image: meme16
    },
    {
      image: meme17
    },
    {
      image: meme18
    },
    {
      image: meme19
    },
  ]

  return (
    <>
    { value == 0 ? 
        <MemePage memes={memes}/>    
    : value == 1 ?
        <Slideshow memes={memes} />
    : value == 2 ?
        <RandomMeme memes={memes} />
    : <p>Sorry little mouse, something went wrong. Try to click an item in the menu or maybe try to refresh. If neither works then
        you can let me know and maybe I can take a look at it.</p>}
    <BottomNavigation
      value={value}
      onChange={(event, newValue) => {
        setValue(newValue);
      }}
      showLabels
      className={classes.root}
    >
      <BottomNavigationAction label="List" icon={<ListIcon />} />
      <BottomNavigationAction label="Slideshow" icon={<SlideshowIcon />} />
      <BottomNavigationAction label="Random meme" icon={<ImageIcon />} />
    </BottomNavigation>
    </>
  );
}