import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
    container: {
        width: "65%",
        margin: "0 auto",
        textAlign: "left"
    },
}));

function Letter() {
    const classes = useStyles();
    return (
        <div className={classes.container}>
            <p>Here I just wrote down some stuff, like I have down a few times. Just think about you and let my mind flow. It's nothing big but I hope you find it nice to read.
                Or at least not dislike it, I like to write so at least one of us will have still enjoyed it then.
            </p>
            <p>
                Miia, as you know I like you. I like you a hecking lot. Has been around a year now since I started thinking about whether my feelings for you are maybe more than just friendship.
                Was a a big thing for me, like my first time falling in love. Besides that also my whole sexuality. Has been a heavy ride with good and less good times. I think I got to know
                myself better by falling in love with you and developed as a person. While writing this I also remember some times I treated you unfairly. I have apologised for it in the past
                but want to do it again. Sorry for those times Miia.
            </p>
            <p>That first paragraph didn't go as planned, is more about me than about you. Miia, I have told you many times already but I want to say it again. You are wonderful!
                I just can't get enough for you. Spending time with you is so nice, even if it's just a stupid useless convo. Just having you with me (even though you are on the other
                side of the world) is so nice. I know it must probably be a bothersome to you that I want to be with you. I also try to leave you alone when you want it or when you're busy
                but I find it hard. I want to spend as much time with you as I can.
            </p>
            <p>
                You are so nice, kind, sweet and caring. Above that you also are really talented and look really beautiful. Like look at your drawings, they are soooo good. Well actually
                calling you talented might not give you the credit that you deserve. You worked really hard to get that good, which deserves a lot of admiration. I already liked your drawings
                back when we met which is two years ago now. But sometimes I look back at your drawings, like go through your twitter and then you can see how much you have improved. It shows
                the effort you have put in it. It's just amazing, you also have gained quite a following already. More than 1000 on Facebook already, is hecking amazing. Also is extra proof
                that your drawings are really nice.
            </p>
            <p>
                I'm also really grateful to have you as a friend. You're always there for me. You don't complain when I write you walls of text. You listen to my problems and try to help me.
                You have helped me with trying to discover my sexuality further. It's also really fun to spend time with you like I said before. Whether we talk, play something, I can watch
                stream while listening to your voice. Hearing your beautiful voice while you sing or hear you play on an instrument. Heck this reminds another amazing thing. You can play 2
                instruments. Piano and ukulele, I know you say you can't play very much on them but I still find it amazing and you're also really good at the ones that you can play. Is just
                so nice to listen to it.
            </p>
        </div>
    );
}

export default Letter;
