import { Card, CardMedia, duration } from '@material-ui/core';
import 'react-slideshow-image/dist/styles.css'
import { Zoom } from 'react-slideshow-image';
import './SlideShow.css';
import { useState } from 'react';
import IconButton from '@material-ui/core/IconButton';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import PauseIcon from '@material-ui/icons/Pause';

function Slideshow({ memes }) {
    const [autoplay, setAutoplay] = useState(true);

    return (
        <>
            <div style={{
                marginTop: "45px",
                width: "85%",
                margin: "auto",

            }}>
                <Zoom indicators={i => (<div className="indicator">{i + 1}</div>)} scale={0.4} duration={10000} autoplay={autoplay}>
                    {memes.map((meme, index) => <img key={index} style={{ maxWidth: "75%", maxHeight: "650px", margin: "auto" }} src={meme.image} />)}
                </Zoom>
                <IconButton style={{marginBottom: "55px"}} aria-label="auto-play" onClick={() => setAutoplay(prev => !prev)}>
                    {autoplay ? <PauseIcon /> : <PlayArrowIcon />}
                </IconButton>
            </div>
        </>
    );
}

export default Slideshow;
