import { Card, CardMedia } from '@material-ui/core';

function MemePage({memes}) {

  return (
    <>
    <h2 style={{display: 'block', color: "#F58D8D"}}>Love you Miia</h2>
    <div style={{
      display: "flex", flexDirection: "row", flexWrap: "wrap",
      alignItems: "center",
      justifyContent: "space-between",
      overflow: "auto",
      margin:"auto",
      marginTop: "1%",
      marginBottom: "3%"
    }}>
      {memes?.map(meme =>
        <Card style={{ width: "30%", height: "350px", margin: "1%", minWidth:"520px" }}>
          <CardMedia component="img" style={{ maxHeight: "345px", padding:"1%", width:"auto",
    margin: "auto", }} image={meme.image} />
        </Card>
      )}
    </div>
    </>
  );
}

export default MemePage;
